html {
  box-sizing: border-box;
  font-size: 0.9375rem;
}

* {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 70px;
  min-height: 100vh;
  position: relative;
  padding-top: 70px;
  padding-bottom: 70px;
  padding-left: 30px;
  padding-right: 30px;
  font-family: "Poppins", sans-serif;
  background-color: #fafafa;
}

.attribution {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: #3e52a3;
}

.cards {
  display: flex;
  flex-direction: column;
  gap: 23px;
  max-width: 375px;
}
@media screen and (min-width: 50em) {
  .cards {
    display: grid;
    grid-template-columns: 315px 315px;
    grid-template-rows: 1fr 1fr;
    max-width: revert;
    grid-template-areas: "1st   2nd" "3rd   4th";
  }
}
@media screen and (min-width: 80em) {
  .cards {
    display: grid;
    grid-template-columns: 350px 350px 350px;
    grid-template-rows: 110px 110px 110px 110px;
    min-width: 1000px;
  }
}
.cards__card {
  padding: 28px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-radius: 7px;
  box-shadow: 0 5px 20px 0px #d9dadd;
  transition: all 0.2s ease;
}
.cards__card:hover {
  transform: scale(1.03);
  cursor: pointer;
}
.cards__card:first-child {
  border-top: 3px #45d3d3 solid;
  grid-area: 1st;
}
@media screen and (min-width: 80em) {
  .cards__card:first-child {
    grid-row: 2/4;
  }
}
.cards__card:nth-child(2) {
  border-top: 3px #ea5353 solid;
  grid-area: 2nd;
}
@media screen and (min-width: 80em) {
  .cards__card:nth-child(2) {
    grid-row: 1/3;
  }
}
.cards__card:nth-child(3) {
  border-top: 3px #fcaf4a solid;
  grid-area: 3rd;
}
@media screen and (min-width: 80em) {
  .cards__card:nth-child(3) {
    grid-row: 3/5;
  }
}
.cards__card:last-child {
  border-top: 3px #549ef2 solid;
  grid-area: 4th;
}
@media screen and (min-width: 80em) {
  .cards__card:last-child {
    grid-row: 2/4;
  }
}
.cards__card h2 {
  font-size: 1.4em;
  font-weight: 600;
  letter-spacing: -0.5px;
  margin-bottom: 7px;
  color: #323653;
}
.cards__card p {
  font-size: 0.9em;
  line-height: 1.6;
  margin-bottom: 30px;
  color: #a3a5ae;
}
.cards__card img {
  height: 60px;
  aspect-ratio: 1;
  align-self: end;
}

.top-info {
  max-width: 325px;
  margin: 0 auto;
  text-align: center;
}
.top-info h1 {
  color: #323653;
  font-size: 1.6em;
  font-weight: 200;
  margin-bottom: 12px;
}
.top-info span {
  color: #323653;
  font-weight: 600;
  margin-bottom: 22px;
}
.top-info p {
  color: #a3a5ae;
  line-height: 1.7;
}
@media screen and (min-width: 80em) {
  .top-info {
    max-width: 505px;
  }
  .top-info h2 {
    font-size: 2.4em;
    margin-bottom: 12px;
  }
  .top-info h1 {
    font-size: 2.4em;
    margin-bottom: 22px;
  }
  .top-info p {
    color: #a3a5ae;
    line-height: 1.7;
  }
}